"use client"

import { useState, useMemo } from 'react'
import Link from 'next/link'
import { useLocalData, Product, OrderItem } from '@/hooks/useLocalData'
import { formatCurrency, calculateOrderTotal, calculateCommission } from '@/lib/utils'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Input } from '@/components/ui/input'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

export default function RestaurantPortal() {
  const { data, loading, error, addOrder } = useLocalData()
  const [cart, setCart] = useState<OrderItem[]>([])
  const [searchTerm, setSearchTerm] = useState('')
  const [categoryFilter, setCategoryFilter] = useState('all')
  const [showCart, setShowCart] = useState(false)

  // Get all products from all farms
  const allProducts = useMemo(() => {
    return data.farms.flatMap(farm => farm.products)
  }, [data.farms])

  // Filter products based on search and category
  const filteredProducts = useMemo(() => {
    return allProducts.filter(product => {
      const matchesSearch = product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                           product.farmName.toLowerCase().includes(searchTerm.toLowerCase())
      const matchesCategory = categoryFilter === 'all' || product.category.toLowerCase() === categoryFilter.toLowerCase()
      return matchesSearch && matchesCategory && product.quantity > 0
    })
  }, [allProducts, searchTerm, categoryFilter])

  // Get unique categories
  const categories = useMemo(() => {
    const cats = [...new Set(allProducts.map(p => p.category))]
    return cats
  }, [allProducts])

  const addToCart = (product: Product, quantity: number = 1) => {
    const existingItem = cart.find(item => item.productId === product.id)
    
    if (existingItem) {
      setCart(cart.map(item => 
        item.productId === product.id 
          ? { ...item, quantity: item.quantity + quantity }
          : item
      ))
    } else {
      const newItem: OrderItem = {
        productId: product.id,
        productName: product.name,
        quantity,
        price: product.price,
        farmId: product.farmId,
        farmName: product.farmName
      }
      setCart([...cart, newItem])
    }
  }

  const removeFromCart = (productId: string) => {
    setCart(cart.filter(item => item.productId !== productId))
  }

  const updateCartQuantity = (productId: string, quantity: number) => {
    if (quantity <= 0) {
      removeFromCart(productId)
      return
    }
    
    setCart(cart.map(item => 
      item.productId === productId 
        ? { ...item, quantity }
        : item
    ))
  }

  const placeOrder = () => {
    if (cart.length === 0) return

    const total = calculateOrderTotal(cart)
    const commission = calculateCommission(total)

    addOrder({
      restaurantId: 'rest-1', // Mock restaurant ID
      restaurantName: 'Farm Table Bistro', // Mock restaurant name
      items: cart,
      status: 'pending',
      orderDate: new Date().toISOString(),
      total,
      commission
    })

    setCart([])
    setShowCart(false)
    alert('Order placed successfully!')
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-64">
        <div className="text-lg">Loading products...</div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-md p-4">
        <div className="text-red-800">Error: {error}</div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Restaurant Portal</h1>
          <p className="text-gray-600">Browse and order fresh products from local farms</p>
        </div>
        <div className="flex space-x-4">
          <Button
            onClick={() => setShowCart(!showCart)}
            variant="outline"
            className="relative"
          >
            Cart ({cart.length})
            {cart.length > 0 && (
              <Badge className="absolute -top-2 -right-2 bg-red-500">
                {cart.reduce((sum, item) => sum + item.quantity, 0)}
              </Badge>
            )}
          </Button>
          <Link href="/restaurant/order-history">
            <Button variant="outline">Order History</Button>
          </Link>
        </div>
      </div>

      {/* Filters */}
      <div className="flex space-x-4">
        <div className="flex-1">
          <Input
            placeholder="Search products or farms..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
        <Select value={categoryFilter} onValueChange={setCategoryFilter}>
          <SelectTrigger className="w-48">
            <SelectValue placeholder="Filter by category" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">All Categories</SelectItem>
            {categories.map(category => (
              <SelectItem key={category} value={category.toLowerCase()}>
                {category}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>

      {/* Cart Sidebar */}
      {showCart && (
        <Card className="mb-6">
          <CardHeader>
            <CardTitle>Shopping Cart</CardTitle>
          </CardHeader>
          <CardContent>
            {cart.length === 0 ? (
              <p className="text-gray-500">Your cart is empty</p>
            ) : (
              <div className="space-y-4">
                {cart.map(item => (
                  <div key={item.productId} className="flex justify-between items-center">
                    <div>
                      <div className="font-medium">{item.productName}</div>
                      <div className="text-sm text-gray-500">{item.farmName}</div>
                    </div>
                    <div className="flex items-center space-x-2">
                      <Input
                        type="number"
                        min="1"
                        value={item.quantity}
                        onChange={(e) => updateCartQuantity(item.productId, parseInt(e.target.value) || 0)}
                        className="w-20"
                      />
                      <span className="font-medium">{formatCurrency(item.price * item.quantity)}</span>
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => removeFromCart(item.productId)}
                      >
                        Remove
                      </Button>
                    </div>
                  </div>
                ))}
                <div className="border-t pt-4">
                  <div className="flex justify-between items-center font-bold">
                    <span>Total: {formatCurrency(calculateOrderTotal(cart))}</span>
                    <Button onClick={placeOrder}>Place Order</Button>
                  </div>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      )}

      {/* Products Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredProducts.map(product => (
          <Card key={product.id} className="hover:shadow-lg transition-shadow">
            <CardHeader>
              <img 
                src={`https://placehold.co/400x300?text=${encodeURIComponent(product.name + ' from ' + product.farmName)}`}
                alt={`${product.name} from ${product.farmName} - fresh local produce`}
                className="w-full h-48 object-cover rounded-md mb-4"
                onError={(e) => {
                  e.currentTarget.onerror = null;
                  e.currentTarget.src = '';
                }}
              />
              <div className="flex justify-between items-start">
                <CardTitle className="text-lg">{product.name}</CardTitle>
                <Badge variant={product.seasonal ? "default" : "secondary"}>
                  {product.seasonal ? "Seasonal" : "Year-round"}
                </Badge>
              </div>
              <CardDescription>{product.description}</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Farm:</span>
                  <span className="font-medium">{product.farmName}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Price:</span>
                  <span className="font-bold text-green-600">{formatCurrency(product.price)}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Available:</span>
                  <span className={`font-medium ${product.quantity < 10 ? 'text-red-600' : 'text-green-600'}`}>
                    {product.quantity} units
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">Category:</span>
                  <Badge variant="outline">{product.category}</Badge>
                </div>
              </div>
            </CardContent>
            <CardFooter>
              <Button 
                onClick={() => addToCart(product)}
                className="w-full"
                disabled={product.quantity === 0}
              >
                {product.quantity === 0 ? 'Out of Stock' : 'Add to Cart'}
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>

      {filteredProducts.length === 0 && (
        <div className="text-center py-12">
          <div className="text-gray-500 text-lg">No products found matching your criteria</div>
          <p className="text-gray-400 mt-2">Try adjusting your search or filter settings</p>
        </div>
      )}
    </div>
  )
}
