"use client"

import { useState, useEffect, useCallback } from 'react'

export interface Product {
  id: string
  name: string
  price: number
  quantity: number
  description: string
  farmId: string
  farmName: string
  category: string
  seasonal: boolean
  harvestDate?: string
}

export interface Farm {
  id: string
  name: string
  owner: string
  location: string
  email: string
  phone: string
  products: Product[]
}

export interface Restaurant {
  id: string
  name: string
  owner: string
  location: string
  email: string
  phone: string
}

export interface OrderItem {
  productId: string
  productName: string
  quantity: number
  price: number
  farmId: string
  farmName: string
}

export interface Order {
  id: string
  restaurantId: string
  restaurantName: string
  items: OrderItem[]
  status: 'pending' | 'confirmed' | 'delivered' | 'cancelled'
  orderDate: string
  deliveryDate?: string
  total: number
  commission: number
}

export interface LocalData {
  farms: Farm[]
  restaurants: Restaurant[]
  orders: Order[]
  lastUpdated: string
}

