export function useLocalData() {
  const [data, setData] = useState<LocalData>(defaultData)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  // Load data from localStorage on mount
  useEffect(() => {
    try {
      const stored = localStorage.getItem('farmfresh-data')
      if (stored) {
        const parsedData = JSON.parse(stored)
        setData(parsedData)
      } else {
        // Initialize with default data
        localStorage.setItem('farmfresh-data', JSON.stringify(defaultData))
      }
    } catch (err) {
      console.error('Error loading data from localStorage:', err)
      setError('Failed to load data')
    } finally {
      setLoading(false)
    }
  }, [])

  // Save data to localStorage
  const saveData = useCallback((newData: LocalData) => {
    try {
      const dataToSave = {
        ...newData,
        lastUpdated: new Date().toISOString()
      }
      localStorage.setItem('farmfresh-data', JSON.stringify(dataToSave))
      setData(dataToSave)
      setError(null)
    } catch (err) {
      console.error('Error saving data to localStorage:', err)
      setError('Failed to save data')
    }
  }, [])

  // Update specific section of data
  const updateData = useCallback((section: keyof Omit<LocalData, 'lastUpdated'>, newSectionData: any) => {
    const updatedData = {
      ...data,
      [section]: newSectionData
    }
    saveData(updatedData)
  }, [data, saveData])

  // Add new order
  const addOrder = useCallback((order: Omit<Order, 'id'>) => {
    const newOrder: Order = {
      ...order,
      id: `order-${Date.now()}`
    }
    const updatedOrders = [...data.orders, newOrder]
    updateData('orders', updatedOrders)
  }, [data.orders, updateData])

  // Update product inventory
  const updateProductInventory = useCallback((farmId: string, productId: string, newQuantity: number) => {
    const updatedFarms = data.farms.map(farm => {
      if (farm.id === farmId) {
        return {
          ...farm,
          products: farm.products.map(product => 
            product.id === productId 
              ? { ...product, quantity: newQuantity }
              : product
          )
        }
      }
      return farm
    })
    updateData('farms', updatedFarms)
  }, [data.farms, updateData])

  // Add new product to farm
  const addProduct = useCallback((farmId: string, product: Omit<Product, 'id' | 'farmId' | 'farmName'>) => {
    const farm = data.farms.find(f => f.id === farmId)
    if (!farm) return

    const newProduct: Product = {
      ...product,
      id: `prod-${Date.now()}`,
      farmId,
      farmName: farm.name
    }

    const updatedFarms = data.farms.map(f => 
      f.id === farmId 
        ? { ...f, products: [...f.products, newProduct] }
        : f
    )
    updateData('farms', updatedFarms)
  }, [data.farms, updateData])

  return {
    data,
    loading,
    error,
    updateData,
    addOrder,
    updateProductInventory,
    addProduct,
    saveData
  }
}
