import { type ClassValue, clsx } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

// CSV Export Utility Function
export function exportToCSV(data: any[], filename: string) {
  try {
    if (!data || data.length === 0) {
      throw new Error('No data to export')
    }

    // Get headers from the first object
    const headers = Object.keys(data[0])
    
    // Create CSV content
    const csvContent = [
      // Header row
      headers.join(','),
      // Data rows
      ...data.map(row => 
        headers.map(header => {
          const value = row[header]
          // Handle values that might contain commas or quotes
          if (typeof value === 'string' && (value.includes(',') || value.includes('"'))) {
            return `"${value.replace(/"/g, '""')}"`
          }
          return value || ''
        }).join(',')
      )
    ].join('\n')

    // Create blob and download
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' })
    const link = document.createElement('a')
    
    if (link.download !== undefined) {
      const url = URL.createObjectURL(blob)
      link.setAttribute('href', url)
      link.setAttribute('download', `${filename}.csv`)
      link.style.visibility = 'hidden'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      URL.revokeObjectURL(url)
    }
  } catch (error) {
    console.error('Error exporting CSV:', error)
    throw new Error('Failed to export CSV file')
  }
}

// Format currency
export function formatCurrency(amount: number): string {
  return new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD'
  }).format(amount)
}

// Format date
export function formatDate(dateString: string): string {
  return new Date(dateString).toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric'
  })
}

// Generate email mailto link
export function generateEmailLink(
  to: string,
  subject: string,
  body: string
): string {
  const params = new URLSearchParams({
    subject: subject,
    body: body
  })
  return `mailto:${to}?${params.toString()}`
}

// Calculate order total
export function calculateOrderTotal(items: { quantity: number; price: number }[]): number {
  return items.reduce((total, item) => total + (item.quantity * item.price), 0)
}

// Calculate commission (10% default)
export function calculateCommission(total: number, rate: number = 0.1): number {
  return total * rate
}
